.robot-card {
    display: flex;
    flex-direction: column;
    flex-basis: 15rem;
    flex-grow: 1;
    flex-shrink: 1;
    min-width: 10rem;
    max-width: 20rem;

    opacity: 0.9;
}

.card-username {
    color: black;
    background: inherit;
    -webkit-background-clip: border-box;
    -webkit-text-fill-color: inherit;
}

